var amountValidationMethod = {

amountValidation:function (val)
	{
	var retVal = true;
	var reg = new RegExp('^[0-9]+(\.[0-9]{1,2})?$'); // regex for numbers with two decimals assuming amount value to have upto two decimal 
		if(val != ''){
			if (!reg.test(val)) {
				retVal=false;
			}
		}
		else{
			retVal = false; // empty value amount
		}
	return retVal;
	}

}

module.exports = amountValidationMethod;